.profile__profession {
  color: white;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.2;
  margin-top: 10px;
  text-overflow: ellipsis;
  max-width: 400px;
  overflow: hidden;
  white-space: nowrap;
}

@media screen and (max-width: 900px) {
  .profile__profession {
      margin: 14px auto 0;
      text-align: center;
      font-size: 14px;
  }
}
