.profile__user-name {
  font-size: 42px;
  font-weight: 500;
  line-height: 1.15;
  color: white;
  margin: 0;
  text-overflow: ellipsis;
  max-width: 400px;
  overflow: hidden;
  white-space: nowrap;
  padding-right: 18px;
}

@media screen and (max-width: 900px) {
  .profile__user-name {
      max-width: 225px;
      text-align: center;
      font-size: 27px;
      padding-right: 10px;
  }
}
