.popup__save-button {
  width: 358px;
  height: 50px;
  margin: 48px 36px 37px 36px;
  background-color: black;
  border-radius: 2px;
  color: white;
  cursor: pointer;
}

.popup__save-button:hover {
  opacity: 0.8;
}

@media screen and (max-width: 500px) {
  .popup__save-button {
      width: 238px;
      height: 46px;
      margin: 45px 22px 25px 22px;
  }
}
